<template>
  <div class="buy-movie-tick" :style="{backgroundImage: 'url(' + data.images.large + ')'}">
    <div class="top"></div>
    <div class="seat-selection">
      <van-nav-bar
        title="座位选择"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
      />
    </div>
    <div class="cinema-ticket">
        <!-- 电影票 -->
        <div class="seat-box">
          <div class="tick-box clearfix" :style="{width: width + 'px'}">
            <div ref="userchair" class="userchair fl" v-for="(item, index) in userChairData" :key="index">
              <i></i><i></i>
              <span>{{item.row}}排{{item.col}}座</span>
            </div>
          </div>
        </div>

        <!-- 银屏 -->
        <div class="silver-screen">
          <div class="screen"></div>
          <div class="silver"></div>
        </div>

        <!-- 座位 -->
        <div class="chair-box">
          <div class="chair chair1" v-for="(item, index) in chairDatas" :key="index" :class="index == 0 ? 'chair1' : index == 6 ? 'chair3' : 'chair2'">
            <span v-for="(value, key) in item" :key="key" :class="{empty: index > 0 && index < 6 && key == 2 || key == 7, buying: value.type == 'buying', selected: value.type == 'selected'}" @click="selectChair(value)"></span>
          </div>
        </div>
        <!-- 座位标志 -->
        <div class="seat-sign clearfix">
          <div class="fl sold clearfix already">
            <span></span>
            <span>已售</span>
          </div>  
          <div class="fl selected clearfix already">
            <span></span>
            <span>已选</span>
          </div>
          <div class="fl optional clearfix already">
            <span></span>
            <span>可选</span>
          </div>
        </div>

        <!-- 购票 -->
        <div class="buy-ticket clearfix">
          <div class="fl">
            <div class="unit-price clearfix">
              <span class="fl">单价：￥20.00</span>
              <span class="fl"> x5</span>
            </div>
            <div class="total">
              <span>合计：￥100.00</span>
            </div>
          </div>
          <div  class="fr pay">确认支付</div>
        </div>
    </div>
  </div>
</template>

<script>

  import {createNamespacedHelpers} from 'vuex';

  const {mapState, mapMutations} = createNamespacedHelpers('buyMovieTickModule');

  export default {
    name: 'buyMovieTick',

    computed: {
      ...mapState(['chairDatas', 'data', 'userChairData', 'width'])
    },

    methods: {
      ...mapMutations(['resetData']),

      // 返回上一步
      onClickLeft() {
        this.$router.go(-1);
      },

      //选择座位
      selectChair(item) {
        // console.log('item :', item);

        //不能选择已经购买的
        if (item.type == 'selected') {
          return;
        }

        if (item.type == '') {
          item.type = 'buying'

          //提交mutations
          this.$store.commit('buyMovieTickModule/changeChairData', item);

          //提交actions
          this.$store.dispatch('buyMovieTickModule/addUserChairData', item)
          .then(() => {
            let firstLi = this.$refs.userchair[0];
            let width = firstLi.offsetWidth;
            let marginRight = parseFloat(getComputedStyle(firstLi).marginRight);
            this.$store.commit('buyMovieTickModule/modifyUlWidth', (width + marginRight) *  this.userChairData.length);
          })
        } else if (item.type == 'buying') {

          //已选，取消选择
          item.type = '';
          this.$store.commit('buyMovieTickModule/changeChairData', item);

          this.$store.dispatch('buyMovieTickModule/deleteUserChairData', item)
            .then(() => {
              let firstLi = this.$refs.userchair[0];
              // console.log('firstLi :', firstLi);

              if (!firstLi) {
                this.$store.commit('buyMovieTickModule/modifyUlWidth', 0);
                return;
              }

              let width = firstLi.offsetWidth;
              let marginRight = parseFloat(getComputedStyle(firstLi).marginRight);
              this.$store.commit('buyMovieTickModule/modifyUlWidth', (width + marginRight) *  this.userChairData.length);
            })
        }
      }
    },

    created() {
      //重置state的data
      this.resetData();

      //开启加载提示
      this.$toast.loading({
        duration: 0,
        message: '加载中...'
      });

      //截取查询参数
      let queryId = this.$route.query.id;
      // console.log('queryId :', queryId);

      //请求
      this.axios({
        method: 'GET',
        url: 'https://douban.uieee.com/v2/movie/subject/' + queryId
      }).then(result => {
        
        this.$store.commit('buyMovieTickModule/getData', result.data);

        this.$toast.clear();
      }).catch(err => {
        
        this.$toast.clear();
      })
    }
  }
</script>

<style lang="less" scoped>
  .buy-movie-tick {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: no-repeat center center;
    background-size: 100% 100%;
    .top {
      height: 24px;
      background-color: #000;
    }
    // .van-nav-bar {
    //   // background-color: transparent;
    // }
    .seat-selection {
      height: 180px;
    }

    .cinema-ticket {
      background-color: #fff;
      height: calc(~"100% - 204px");
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
      padding: 30px 10px 0;
    }

    .seat-box {
      overflow-x: auto;
      overflow-y: hidden;
      height: 36px;
    }
    .tick-box {
      width: 530px;
    }
    .userchair {
      width: 80px;
      height: 36px;
      line-height: 36px;
      font-size: 16px;
      text-align: center;
      color: #fff;
      background-color: #FF9800;
      position: relative;
      overflow: hidden;
      margin-right: 10px;
      &:last-child {
        margin-right: 0;
      }

      i {
        position: absolute;
        top: 12px;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: #fff;
        overflow: hidden;
        &:first-child {
          left: -6px;
        }
        &:nth-child(2) {
          right: -6px;
        }
      }
    }

    .silver-screen {
      position: relative;
      width: 300px;
      margin: 0 auto;
      height: 50px;
      overflow: hidden;

      .screen {
        position: absolute;
        top: 15px;
        left: -50px;
        width: 400px;
        height: 120px;
        border: 3px solid #FFAE00;
        border-radius: 50%;
      }
      .silver {
        position: absolute;
        top: 19px;
        left: -50px;
        width: 400px;
        height: 120px;
        border: 3px solid rgba(255, 243, 199, .6);
        border-radius: 50%;
      }
    }

    .chair-box {
      .chair {
        overflow: hidden;
        margin: 6px auto 0;
        &.chair1 {
          width: 96px;
        }

        &.chair2 {
          width: 352px;
          span {
            &.empty {
              margin-left: 36px;
            }
          }
        }

        &.chair3 {
          width: 160px;
        }
      }

      span {
        width: 24px;
        height: 24px;
        // background-color: #FF9800;
        float: left;
        margin: 0 4px;
        background: url('../assets/images/chair2.png') no-repeat center center;
        background-size: 100% 100%;
         &.selected{
          background-image: url("../assets/images/chair1.png")
        }
        &.buying{
          background-image: url("../assets/images/chair3.png")
        }
      }
    }

    .seat-sign {
      height: 30px;
      line-height: 30px;
      width: 270px;
      margin: 20px auto 0;
      text-align: center;

      .already {
        margin: 0 20px;
        width: 50px;
        span {
          float: left;
          &:first-child {
            margin: 12px 6px 0 0;
            width: 16px;
            height: 10px;
            display: block;
          }
        }
      }
      .sold {
        span {
          &:first-child {
            background-color: #E7E6E5;
          }
        }
      }
      .selected {
        span {
          &:first-child {
            background-color: #FFBC32;
          }
        }
      }
      .optional {
        span {
          &:first-child {
            background-color: #686258;
          }
        }
      }
    }

    .buy-ticket {
      border-top: 3px solid #ccc;
      margin-top: 20px;
      height: 60px;
      padding-top: 8px;
      .unit-price {
        span {
          line-height: 20px;
          margin-right: 4px;
        }
      }
      .total {
        span {
          line-height: 26px;
          font-size: 14px;
          color: #FF9800;
        }
      }

      .pay {
        width: 100px;
        height: 32px;
        line-height: 32px;
        text-align: center;
        font-size: 16px;
        color: #fff;
        background-color: #FED13F;
        border-radius: 8px;
        box-shadow: 0px 0px 5px 1px #ccc;
        margin-top: 6px;
      }
    }
  }
</style>